<template>
  <div id="error-view-container" style="text-align: center">
    <div>
      <h1 id="auto-refresh-count-down">{{ countDown }}</h1>
      <h1 id="error-status">Http {{ status }} {{ phrase }}</h1>
      <h2 id="error-sub-title-text">
        {{
          $route.params.pathMatch !== undefined ? "页面不存在" :
              $route.query.message === undefined ? "系统似乎遇到了一些问题" : $route.query.message
        }}
        <router-link to="/">
          <el-tooltip class="item" content="返回首页" effect="dark" placement="top">
            <i class="el-icon-refresh-left"></i></el-tooltip>
        </router-link>
      </h2>
    </div>
  </div>
</template>

<script>
import router from "../router";

export default {
  name: "ErrorView",
  data() {
    return {
      countDown: null,
      timerId: null,
      status: null,
      phrase: null,
      userSideError: false
    }
  },
  methods: {
    refresh: function () {
      router.push("/")
    },
    start: function () {
      this.countDown = 10;
      this.timerId = setInterval(
          () => {
            --this.countDown === 0 ? this.refresh() : null
          },
          1000
      )
    },
    stop: function () {
      if (this.timerId) {
        clearInterval(this.timerId)
        this.timerId = null
      }
    }
  },
  mounted() {
    if (this.$route.params.pathMatch !== undefined) {
      this.status = 404
      this.phrase = "Not Found"
    } else {
      this.status = this.$route.query.status
      this.phrase = this.$route.query.phrase
    }
    this.stop()
    this.start()
  }
}
</script>

<style scoped>
#error-view-container {
  height: 100vh;
  display: flex;
}

#error-view-container > div {
  margin: auto;
}

#auto-refresh-count-down {
  color: aliceblue;
  font-size: 60pt;
}

#error-status {
  color: aliceblue;
  font-size: 40pt;
  border-bottom: 1px solid white;
}

#error-sub-title-text {
  color: aliceblue;
}

.el-icon-refresh-left {
  color: aliceblue;
}
</style>