<template>
  <div id="password-recovery-container">
    <div>
      <span class="content"><principal-input v-if="currentState === state.PRINCIPAL"
                                             ref="principalInput"></principal-input></span>
      <span class="content"><code-input v-if="currentState === state.CODE" ref="codeInput"></code-input></span>
      <span class="content"><reset-password-input v-if="currentState === state.RESET"
                                                  ref="codeInput" :token="this.token"></reset-password-input></span>
      <span class="content as-button"><el-link v-if="this.currentState !== this.state.PRINCIPAL" :underline="false"
                                               @click="home">返回 </el-link></span>
      <span v-if="currentState!==this.state.PRINCIPAL && currentState !== this.state.RESET" class="slash"></span>
      <span class="content as-button"><el-link v-if="this.currentState !== this.state.RESET" :underline="false"
                                               @click="next">下一步</el-link></span>
    </div>
  </div>
</template>

<script>
import PrincipalInput from "@/components/password-recovery/PrincipalInput";
import CodeInput from "@/components/password-recovery/CodeInput";
import basicAxiosProxy from "@/xhr/axios-aop";
import ResetPasswordInput from "@/components/password-recovery/ResetPasswordInput";
import PasswordRecoveryLoadingVeil from "@/components/password-recovery/veil/PasswordRecoveryLoadingVeil";

const State = {
  PRINCIPAL: "PRINCIPAL",
  CODE: "CODE",
  RESET: "RESET"
}
export default {
  name: "PasswordRecovery",
  components: {ResetPasswordInput, CodeInput, PrincipalInput},
  data() {
    return {
      currentState: State.PRINCIPAL,
      state: State,
      token: ''
    }
  },
  methods: {
    home: function () {
      this.currentState = State.PRINCIPAL;
      this.token = ''
    },
    next: function () {
      let loadingInstance
      switch (this.currentState) {
        case this.state.PRINCIPAL:
          loadingInstance = this.$loading(Object.assign(PasswordRecoveryLoadingVeil, {text: "正在发送验证码"}))
          basicAxiosProxy.post(
              "/api/me/password:recover",
              {
                principalType: this.$refs.principalInput.type,
                username: this.$refs.principalInput.name
              }
          ).then(
              response => {
                this.token = response.data.challengeId
                this.currentState = State.CODE
              }
          ).finally(
              () => {
                loadingInstance.close()
              }
          )
          break
        case this.state.CODE:
          loadingInstance = this.$loading(Object.assign(PasswordRecoveryLoadingVeil, {text: "正在验证"}))
          basicAxiosProxy.post(
              "/api/me/password:response",
              {
                challengeId: this.token,
                response: this.$refs.codeInput.code
              }
          ).then(
              () => {
                this.currentState = State.RESET
              }
          ).finally(
              () => {
                loadingInstance.close()
              }
          )
      }
    }
  }
}
</script>

<style scoped>
#password-recovery-container {
  display: flex;
  height: 100vh;
  width: 100%;
  text-align: center;
}

#password-recovery-container > div {
  margin: auto;
  text-align: center;
  display: inline;
  width: 100%;
  color: aliceblue;
}

#password-recovery-container > div > span.content {
  display: inline-block;
}

/deep/ .el-link--inner {
  color: aliceblue;
}

span.slash {
  border-right: 1px solid white;
  height: 100%;
  padding-left: 5px;
  margin-right: 5px;
}

span.as-button {
  padding-left: 3px;
  padding-right: 3px;
}
</style>