<template>
  <el-form ref="usernamePasswordForm" :model="formItems" :rules="validation" action="/login" method="POST">
    <input name="authenticationType" type="hidden" value="username"/>
    <el-form-item prop="username">
      <el-input
          v-model="formItems.username" name="username" placeholder="输入用户名/手机号/会员卡号" prefix-icon="el-icon-user">
      </el-input>
    </el-form-item>
    <el-form-item prop="password">
      <el-input v-model="formItems.password" name="password" placeholder="输入密码" prefix-icon="el-icon-key" show-password
                type="password"></el-input>
    </el-form-item>
    <el-form-item>
      <el-button id="username-longin-button" plain type="primary">登录</el-button>
    </el-form-item>
    <router-link id="forget-password" to="/password/recovery">忘记密码?</router-link>
  </el-form>
</template>

<script>
import LoginLoadingVeil from "../veil/LoginLoadingVeil";

export default {
  name: "UsernamePasswordForm",
  data() {
    return {
      formItems: {
        username: "",
        password: ""
      },
      validation: {
        username: [{required: true, message: "请输入用户名", trigger: 'blur'}],
        password: [{required: true, message: "请输入密码", trigger: 'blur'}]
      }
    }
  },
  methods: {
    login: function () {
      this.$loading(LoginLoadingVeil)
      this.$refs.usernamePasswordForm.$el.submit()
    }
  },
  mounted() {
    new window['TencentCaptcha'](
        document.getElementById("username-longin-button"),
        2048219257,
        response => {
          if (response.ret === 0) {
            this.$refs.usernamePasswordForm.validate(
                isValid => {
                  if (isValid) {
                    this.login()
                  } else {
                    return false
                  }
                }
            )
          } else {
            this.$refs.usernamePasswordForm.validate(
                isValid => {
                  if (isValid) {
                    this.login()
                  } else {
                    return false
                  }
                }
            )
          }
        }
    )
  }
}
</script>

<style scoped>
#username-longin-button {
  width: 100%;
}

/deep/ .el-form-item__error {
  color: aliceblue;
}

#forget-password {
  color: aliceblue;
  font-size: 10pt;
  width: 100%;
  text-align: right;
  display: block;
}
</style>