<template>
  <div id="login-forms">
    <div id="login-forms-container">
      <h1 id="login-forms-header">Camellia统一认证系统</h1>
      <div id="login-forms-body">
        <div>
          <el-row :gutter="20">
            <el-tooltip v-if="authenticationTypes.includes('username')" class="item" content="用户名密码登录" effect="dark"
                        placement="top">
              <el-col :class="loginForm === 'username' ? 'active-form' : null" :span="24/authenticationTypes.length"
                      @click.native="selectUsernamePasswordForm">
                <i class="el-icon-lock login-form-selector"></i>
              </el-col>
            </el-tooltip>
            <el-tooltip v-if="authenticationTypes.includes('sms')" class="item" content="短信验证码登录" effect="dark"
                        placement="top">
              <el-col :class="loginForm === 'sms' ? 'active-form' : null" :span="24/authenticationTypes.length"
                      @click.native="selectSmsForm">
                <i class="el-icon-message login-form-selector"></i>
              </el-col>
            </el-tooltip>
            <el-tooltip v-if="authenticationTypes.includes('qrcode')" class="item" content="手机扫码登录" effect="dark"
                        placement="top">
              <el-col :class="loginForm === 'qrcode' ? 'active-form' : null"
                      :span="24/authenticationTypes.length"
                      @click.native="selectQrcodeForm">
                <i class="el-icon-mobile-phone login-form-selector"></i>
              </el-col>
            </el-tooltip>
          </el-row>
        </div>
        <div id="login-forms-body-container">
          <UsernamePasswordForm v-if="loginForm === 'username'"></UsernamePasswordForm>
          <MobilePhoneSmsCodeForm v-if="loginForm === 'sms'"></MobilePhoneSmsCodeForm>
          <QrCodeForm v-if="loginForm === 'qrcode'"></QrCodeForm>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import QrCodeForm from "@/components/login/form/QrCodeForm";
import MobilePhoneSmsCodeForm from "@/components/login/form/MobilePhoneSmsCodeForm";
import UsernamePasswordForm from "@/components/login/form/UsernamePasswordForm";
import basicAxiosProxy from "@/xhr/axios-aop";

export default {
  name: "LoginForm",
  components: {UsernamePasswordForm, MobilePhoneSmsCodeForm, QrCodeForm},
  data() {
    return {
      loginForm: null,
      authenticationTypes: []
    }
  },
  methods: {
    selectUsernamePasswordForm: function () {
      this.loginForm = "username";
    },
    selectSmsForm: function () {
      this.loginForm = "sms"
    },
    selectQrcodeForm: function () {
      this.loginForm = "qrcode"
    }
  },
  mounted() {
    basicAxiosProxy.get("/api/options").then(
        response => {
          this.authenticationTypes = response.data.options["authenticationTypeRegistry"].option.types
          this.loginForm = this.authenticationTypes[0]
        }
    )
  }
}
</script>
