<template>
  <div id="login-view-container">
    <login-form v-show="authenticated !== null && !authenticated"></login-form>
  </div>
</template>

<script>

import LoginForm from "@/components/login/LoginForm";
import basicAxiosProxy from "@/xhr/axios-aop";
import router from "@/router";

export default {
  name: "LoginView",
  components: {LoginForm},
  data() {
    return {
      authenticated: null
    }
  },
  mounted() {
    const loading = this.$loading({
      lock: true,
      text: '正在检查登录状态，清稍候...',
      spinner: 'el-icon-loading',
      background: 'rgba(0, 0, 0, 0.4)',
      customClass: "authentication-check"
    })
    basicAxiosProxy.get("/api/me").then(
        () => {
          this.authenticated = true
          loading.close()
          router.push("/welcome")
        }
    ).catch(
        () => {
          this.authenticated = false
          loading.close()
        }
    )
  }
}
</script>
<style scoped>
.authentication-check p.el-loading-text {
  color: aliceblue;
}

.authentication-check i.el-icon-loading {
  color: aliceblue;
}
</style>