<template>
  <div id="consent-view-container">
    <div>
      <h1>应用程序<span id="client-name-container">{{ clientId }}</span>需要访问您的以下信息，请确认是否授权</h1>
      <div id="scopes-container"><span v-for="(scope, index) in safeGetScopes()" :key="index"><i
          class="el-icon-check"/>{{ scope }}</span></div>
      <div id="action-container">
        <el-form ref="openIdConsentForm" action="/oauth2/authorize" method="POST">
          <input :value="clientId" name="client_id" readonly type="hidden"/>
          <input v-for="(scope, index) in safeGetScopes()" :key="index" :value="scope" name="scope" readonly
                 type="hidden"/>
          <input :value="state" name="state" readonly type="hidden"/>
          <el-button v-show="!consentButtonClicked" icon="el-icon-check" type="success" @click="approve">授权</el-button>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
import router from "@/router";

export default {
  name: "ConsentView",
  data() {
    return {
      clientId: "",
      state: "",
      scopes: "",
      consentButtonClicked: false
    }
  },
  mounted() {
    this.clientId = this.$route.query.client_id
    this.state = this.$route.query.state
    this.scopes = this.$route.query.scope
    if (this.clientId === undefined || this.clientId.trim() === '' || this.safeGetScopes().length === 0 || this.state === undefined || this.state.trim() === '') {
      router.push({
        name: "error",
        query: {status: 403, phrase: "Forbidden", message: "应用的权限申请不合法，请不要通过收藏夹或直接输入网址的方式打开当前页面"}
      })
    }
  },
  methods: {
    safeGetScopes: function () {
      if (this.scopes !== undefined) {
        return this.scopes.split(' ')
      }
      return [];
    },
    /**
     * 同意
     */
    approve: function () {
      this.consentButtonClicked = true
      this.$refs.openIdConsentForm.$el.submit()
    }
  }
}
</script>

