<template>
  <div>
    <el-row>
      <el-col :span="6">{{ "  1  " }}</el-col>
      <el-col :span="12">
        <el-input v-model="code" placeholder="请输入发送给您的短信验证码">
          <el-button slot="append" plain type="primary" @click="authenticate">继续</el-button>
        </el-input>
      </el-col>
      <el-col :span="6"></el-col>
    </el-row>
    <mfa-form ref="mfaForm"></mfa-form>
  </div>
</template>

<script>
import MfaForm from "../login/form/MfaForm";

export default {
  name: "SmsAuthenticator",
  components: {MfaForm},
  data() {
    return {
      code: "",
      challengeId: this.$route.query.challengeId
    }
  },
  methods: {
    authenticate: function () {
      this.$refs.mfaForm.authenticate(this.challengeId, this.code)
    }
  }
}

</script>

