<template>
  <el-form ref="mobilePhoneForm" action="/login" method="POST">
    <input name="authenticationType" type="hidden" value="sms"/>
    <el-row>
      <el-input
          v-model="mobilePhoneNumber"
          name="mobilePhoneNumber"
          placeholder="输入您的手机号" prefix-icon="el-icon-mobile">
        <el-button id="acquire-sms-code-button" slot="append" :disabled="coolDownSeconds > 0">
          获取验证码{{ coolDownSeconds > 0 ? '(' + coolDownSeconds + ')' : null }}
        </el-button>
      </el-input>
    </el-row>
    <el-row>
      <el-input v-model="code" name="code" placeholder="输入验证码"></el-input>
    </el-row>
    <el-row>
      <el-button id="sms-longin-button" plain type="primary" @click="login">登录</el-button>
    </el-row>
  </el-form>
</template>

<script>
import CommonLoadingVeil from "../veil/LoginLoadingVeil";
import basicAxiosProxy from "../../../xhr/axios-aop";

export default {
  name: "MobilePhoneSmsCodeForm",
  data() {
    return {
      mobilePhoneNumber: "",
      code: "",
      coolDownSeconds: 0,
      timerId: null
    }
  },
  methods: {
    coolDown: function (coolDownSeconds) {
      this.coolDownSeconds = coolDownSeconds
      this.timerId = setInterval(
          () => {
            --this.coolDownSeconds
            if (this.coolDownSeconds === 0) {
              clearInterval(this.timerId)
              this.timerId = null
            }
          },
          1000
      )
    },
    login: function () {
      this.$loading(CommonLoadingVeil)
      this.$refs.mobilePhoneForm.$el.submit()
    }
  },
  mounted() {
    new window['TencentCaptcha'](
        document.getElementById("acquire-sms-code-button"),
        2048219257,
        response => {
          console.log(response);
          basicAxiosProxy.post("/api/authentication/sms", {
            mobilePhoneNumber: this.mobilePhoneNumber,
            captchaToken: response.ticket
          }).then(
              response => {
                this.coolDown(response.data.cooldown);
              }
          ).catch(
              error => {
                let response = error.response
                console.error(response)
                if (response.data !== undefined && response.data.error !== undefined) {
                  if (response.data.status === 429) {
                    basicAxiosProxy.get("/api/authentication/sms?mobilePhoneNumber=" + this.mobilePhoneNumber).then(
                        response => {
                          this.coolDown(response.data.cooldown)
                        }
                    )
                  }
                }
              }
          )
        }
    )
  }
}
</script>

<style scoped>
/deep/ .el-row {
  margin-bottom: 5px;
}

#sms-longin-button {
  width: 100%;
}
</style>