<template>
  <div id="reset-password-container">
    <span><el-input v-model="password" placeholder="请输入新的密码" type="password"></el-input></span>
    <span><el-input v-model="confirmed" placeholder="请确认您的密码" type="password"></el-input></span>
    <span><el-button primary @click="resetPassword">修改</el-button></span>
  </div>
</template>

<script>
import basicAxiosProxy from "@/xhr/axios-aop";
import RetrievePasswordLoadingVeil from "@/components/password-recovery/veil/PasswordRecoveryLoadingVeil";

export default {
  name: "ResetPasswordInput",
  props: ["token"],
  data() {
    return {
      password: '',
      confirmed: ''
    }
  },
  methods: {
    resetPassword: function () {
      let loading = this.$loading(Object.assign(RetrievePasswordLoadingVeil, {text: "正在重置密码"}))
      basicAxiosProxy.put(
          "/api/me/password", {
            challengeId: this.$props.token,
            password: this.password
          }
      ).then(
          () => {
            this.$notify.success({message: "密码重置完成", title: "成功"})
            this.$router.push("/")
          }
      ).finally(
          () => {
            loading.close()
          }
      )
    }
  }
}
</script>

<style scoped>
#reset-password-container {
}

#reset-password-container > span .el-input {
  display: inline-block;
  width: 260px;
  padding-left: 2px;
  padding-right: 2px;
}

#reset-password-container > span .el-button {
  margin-left: 5px;
  margin-right: 5px;
}
</style>