import Vue from 'vue'
import VueRouter from 'vue-router'
import LoginView from "@/view/LoginView";
import WelcomeView from "@/view/WelcomeView";
import ErrorView from "@/view/ErrorView";
import QrCodeAuthenticationLandingView from "@/view/QrCodeAuthenticationLandingView";
import MfaView from "@/view/MfaView";
import ConsentView from "@/view/ConsentView";
import GoodbyeView from "@/view/GoodbyeView";
import PasswordRecovery from "../components/password-recovery/PasswordRecovery";

Vue.use(VueRouter)

const routes = [
    {path: '/', component: LoginView},
    {path: '/welcome', component: WelcomeView},
    {path: '/mfa', component: MfaView},
    {path: '/consent', component: ConsentView},
    {path: '/error', component: ErrorView, props: true, name: "error"},
    {path: '/qrcode/landing', component: QrCodeAuthenticationLandingView},
    {path: "/password/recovery", component: PasswordRecovery},
    {path: "/goodbye", component: GoodbyeView},
    {path: '*', component: ErrorView},
]

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
})

export default router
